export default {
	onLoad() {
		this.requestUrl()
	},
	data() {
		return {
			totalIncome:998,
			content: []
		}
	},
	methods: {
		requestUrl() {
			uni.request({
				url:'https://www.finetwm.com/xmRepair/order/findShopOrderByStatus',
				method:"POST",
				header:{
					"content-Type": "application/x-www-form-urlencoded"
				},
				data:{
					shopId: 1,
					orderStatus:4
				},
				success:res=>{
					this.content = res.data.data
				}
			})
		}
	}
}
